import Axios from 'axios';
import {servers} from '../keys'
const {devServer} = servers

const registerUser = async (data, setLoading, setActiveAlert,setOpen,setSesion) =>{
  setLoading('cargando...')
    data.preventDefault()
    const {nombre,apellido,correo,password,userPaisOrigen,dni} = data.target
    const newUser ={
        nombre:nombre.value,
        apellido:apellido.value,
        correo:correo.value,
        password:password.value,
        userPaisOrigen:userPaisOrigen.value,
        dni:dni.value,
        fechaIncripcion:Date()
    }
    if(nombre.value==='' || apellido.value==='' || correo.value==='' || password.value==='' || userPaisOrigen.value === '' || dni.value === ''){
      setLoading('Rellena todos los campos')
      return
    }

    const start = await Axios.post(`${devServer}/registerUser`, newUser)
    const res = await start.data
          if(res.status === 'ok'){
            setLoading('Registro Exitoso')
            setActiveAlert(true)
            
          }else{
            setLoading('hubo un error')
          }
          
  }

 
const verifyMail = async (mail, setVerify, setActiveAlert) =>{
  const {value} = mail.target
  
  if(value === '') return setVerify('si')

  const start = await Axios.post(`${devServer}/mailVerify`,{correo:value})
  const res = await start.data

  setActiveAlert(true)
  setVerify(res)
  return
}

const login = async (data,setLoading,btn, setSesion)=>{
      data.preventDefault()
      setLoading('cargando')
      const {correo,password} = data.target
      if(correo.value && password.value){
            const user ={
                  correo:correo.value,
                  password:password.value
              }
            const start = await Axios.post(`${devServer}/loginUser`, user)
            const res = await start.data

            if(res === 'fail'){
              
              return setLoading('error del servidor')
            }else if(res === 'verifique sus datos'){
              
              return setLoading(res)
            }else{
              setLoading('')
              await sessionStorage.setItem('userSesion', JSON.stringify(res))
              
              btn(false)
              setSesion(true)
              window.location = '/Dashboard'
            }
      }else{
        return setLoading('Rellene todos los campos')
      } 

}

const getBanks = async (setBanks,id)=>{
  const gets = await Axios.get(`${devServer}/bankAcounts/${id}`)
  const data = await gets.data
  
  if(gets.status === 200){
    return setBanks(data)
  } 
}
