import React, {useState, useEffect,useLayoutEffect} from 'react'
import {addPais} from '../../../gets_apis/sockets'
import {imgPaises} from '../../../gets_apis/api_sesion'
import TazaActual from '../tazaActual'

const AddPais = ()=>{
    const [loading, setLoading] = useState('')
    const [imgs, setImgs] = useState([{}])
    const [data, setData] = useState({
        nombre:'',
        codArea:'', 
        ico:'', 
        shortName:'',
        moneda:'',
        shortMoneda:'',
        tazaCambio:''
    })

    const setPais = e =>{
        const {alt, codArea, src, shortNombre, moneda, shortMoneda, } = e.target.attributes
       
        const values = {
            nombre:alt.value,
            codArea:codArea.value,
            ico:src.value,
            shortNombre:shortNombre.value,
            moneda:moneda.value,
            shortMoneda:shortMoneda.value,
            tazaCambio:data.tazaCambio
        }

        setData(values) 
    }
    const setValor = e =>{
        const {value } = e.target
        const values = {
            nombre:data.nombre,
            codArea:data.codArea,
            ico:data.ico,
            shortNombre:data.shortNombre,
            moneda:data.moneda,
            shortMoneda:data.shortMoneda,
            tazaCambio:value
        }

        setData(values) 
        console.log(value)
    }
    const add = e =>{
        setLoading('Cargando')
        e.preventDefault()
        if(data.nombre === ''){
            return alert('seleccione un pais')
        }
        if(data.tazaCambio === ''){
            return alert('introduzca la taza de cambio')
        }
        addPais(data,setLoading)
    }
    useLayoutEffect(()=>{    
            imgPaises(setImgs)
    },[])
    useEffect(()=>{
    })
    return(
            <div className='container'>
                <h1>Gestionar monedas</h1>
                <div className='containerTazaActual'>
                    <TazaActual setLoading={setLoading} dir={document.location.pathname}/>
                </div>
                
                <form className="calculadora" onSubmit={add}>
                
                    <h2>Selecciona un pais para comenzar</h2>
                    <div id='paises'>
                        <h2>
                        {
                            imgs.map((img,i)=>(
                                <img
                                src={img.ico}
                                alt={img.nombre}
                                moneda={img.moneda}
                                codArea={img.codArea}
                                shortNombre={img.shortNombre}
                                shortMoneda={img.shortMoneda}
                                onClick={setPais}
                                />
                            ))
                        }
                        </h2>
                    </div>
                    <div >
                        <label>Pais</label>
                        <input type='text' name='nombre' value={data.nombre} readOnly/>

                        <label>Taza de cambio</label>
                        <input type='number' name='tazaCambio' id='taza' onKeyUp={setValor} onChange={setValor}/>
                    </div>
                {loading}
                <div><button className="btnGreen">Agregar</button ></div>
                
            </form>
            </div>        
    )
}

export default AddPais