import React, {useState, useEffect, Fragment} from'react'
import {paises, updatePais} from '../../gets_apis/sockets'
import updatebtn from '../../svg/actualizar.svg'
import './tazaActual.css'

function TazaActual(){
    const [taza,setTaza] = useState([{}])
    const [data, setData] = useState({
      id:'',
      tazaCambio:''
  })

const setValues = e =>{
  const {id} = e.target.attributes
  const newValues = {
    id:id.value,
    tazaCambio:e.target.value
  }
  setData(newValues)
}

const add = e =>{
  e.preventDefault()
  if(data.id === ''){
      return alert('seleccione un pais')
  }else if(data.tazaCambio === ''){
      return alert('verifique si los campos')
  }else{
    updatePais(data)
    
  }
  const forms = document.querySelectorAll('.ActMonedas')
  forms.forEach(form=>{
    form.reset()
  })

}

   useEffect(()=>{
    paises(setTaza)
   },[])

    return document.location.pathname !== '/Dashboard'?(
      <Fragment>
          {
            taza.map((items,i)=>(
              <Fragment>
                <form className="ActMonedas">
                  <img src={items.ico} alt={items.moneda}/>
                  <div>
                      <label>
                         {items.moneda}
                      </label>
                      <input type="number" step="any" id={items.id} onFocus={setValues} onKeyUp={setValues} onChange={setValues} placeholder={items.tazaCambio}/>
                      <span onClick={add}><img src={updatebtn} alt="update"/></span>
                  </div>   
                </form>
              </Fragment>
            ))
          }
      </Fragment>
          
    ):(
      <Fragment>
          <h1>Taza del dia</h1>
          <section className="Monedas">
              {
                taza.map((items, i)=>(
                  <article key={i}>
                    <img src={items.ico} alt={items.nombre} />
                    <h3>1 {items.shortMoneda} = {items.tazaCambio} BsS</h3>
                  </article>
                ))
              }
          </section>
      </Fragment>
    )

} 

export default TazaActual