import React, {Fragment, useState, useEffect} from 'react'
import {getBanks} from '../../gets_apis/api_sesion'
import './bankAcounts.css'


function BankAcount(){
    const [banks, setBanks] = useState(false)

    useEffect(()=>{
        const getUser = JSON.parse(sessionStorage.userSesion)
        getBanks(setBanks,getUser.idUsuario)
    },[])

    return !banks ?(
        <h1>Loading</h1>
    ):(
        <Fragment>
            <div className='container'>
            <h1>cuentas de bancos</h1>
            <section className='containerCards'>
            
            {
               banks.map((items, i)=><article key={i} className="DatosBancarios">
                <h2>{items.banco}</h2>
                <p>
                    <span>{items.paisBanco}</span> {items.titular}
                    <br/>
                    <span>C.I:</span> {items.dniTitular}
                    <br/>
                    <span>Banco:</span> {items.banco}
                    <br/>
                    <span>Tipo de Cuenta:</span> {items.tipoCuenta}
                    <br/>
                    <span>Numero de Cuenta:</span> {items.numeroCuenta}
                </p>
            </article>)
            }
            </section>
            </div>

        {/* <section>
            <h1>Ultimo Paso de transaccion</h1>
            <article className="Paso3">
                <h2>Orden completada</h2>
                <div><img src="img/svg/aprobado%202.svg" alt="" />
                    <h2>Orden pagada al beneficiario</h2>
                </div>
                <div>
                    <h3>Datos</h3>
                    <p>
                        <span>Pagado a:</span> ernesto medina
                        
                        <span>C.I:</span> 85.265.545
                        
                        <span>Monto:</span> 500.000,00 BsS
                        
                        <span>Banco:</span> BBVA provincial
                        
                        <span>ID de orden:</span> 000001
                        
                        <span>Numero de referencia Bancaria:</span> 0240 4024 024264 24 46654
                    </p>
                    <button className="BtnDescarga"><img src="img/svg/descargar1.svg" alt="Descargar libreta" /><label>Descargar</label> </button>
                    <button className="BtnDescarga"><img src="img/svg/detalles.svg" alt="Descargar libreta" /><label>Detalles</label> </button>
                    <button className="BtnDescarga"><img src="img/svg/contrato.svg" alt="Descargar libreta" /><label>Imprimir</label> </button>
                </div>

                <a href="/" className="BtnVerde">Finalizar</a>
            </article>
        </section> */}
        
        </Fragment>
    )
}

export default BankAcount