import React, {useState, useEffect} from 'react'
import {BrowserRouter as Router, Route, Redirect} from 'react-router-dom'

import Header from './components/header'
import Home from './rutas/home'

import PwaFeacture from './components/pwa_feactures'

import Dashboard from './rutas/dashboard'
import BankAcounts from './rutas/dashboard/BankAcounts'
import Headerdashboard from './components/header_dashboard'
import NewOrder from './rutas/dashboard/newOrder'
import History from './rutas/dashboard/history'
import AddBanks from './rutas/dashboard/forms/addBanks'
import AddPais from './rutas/dashboard/forms/addPais'
import VerifyOrder from './rutas/dashboard/forms/verificarOrden'


function App(){
    const [sesion, setSesion] = useState(false)
    const [user, setUser] = useState(false)
    
        useEffect(()=>{
            sessionStorage.userSesion ? setSesion(true) : setSesion(false)
            sessionStorage.userSesion ? setUser(JSON.parse(sessionStorage.userSesion)) : setUser({})
        },[])

         return(
                <Router>
                    <PwaFeacture/>
                    {!sesion ? <Header sesion={sesion} setSesion={setSesion}/> : <Headerdashboard user={user} sesion={sesion} setSesion={setSesion}/>}
                    
                    {!sesion ?(<Route exact path="/" component={Home}/>
                    ):null
                    }    
                        
                        <Route exact path="/Dashboard" component={Dashboard}/>
                        <Route  path="/Dashboard/NewOrder" component={NewOrder}/>
                        <Route  path="/Dashboard/BankAcounts" component={BankAcounts}/>
                        <Route  path="/Dashboard/AddBanks" component={AddBanks}/>
                        <Route  path="/Dashboard/AddPais" component={AddPais}/>
                        <Route  path="/Dashboard/VerifyOrder/:item?" component={VerifyOrder}/>
                        <Route  path="/Dashboard/History" component={History}/>
                    
                </Router>    
                
            )
        
}

export default App