import io from 'socket.io-client'

import {servers} from '../keys'
const {devServer} = servers

function paises(setPaises){
        const socket = io(devServer)
        socket.on('paises', e =>{
            return setPaises(e)
        })
}
const addPais = async (data,setLoading) =>{
    setLoading('cargando')
    const socket = io(devServer)
    
    await socket.emit('newPais',data)
        
}
const updatePais = async (data) =>{
    
    const socket = io(devServer)
    
    await socket.emit('updatePais',data)
    
        
}
const newOfert = async (data,setLoading,setActiveAlert,btn) =>{
    data.preventDefault()
    const datosFecha = new Date()
    const fecha ={
      
        hora:datosFecha.toLocaleDateString()
    }
    setLoading('cargando')
    const {montoDeposito,monedaDeposito,montoRetiro,monedaRetiro,idBanco,idUsuario,tazaCambio,referenciaDeposito} = data.target
    if(idBanco.value === ''){
        setLoading('')
        return alert('seleccione el banco')
    }
    const datosPedido = {
      montoDeposito:montoDeposito.value,
      monedaDeposito:monedaDeposito.value,
      referenciaDeposito:referenciaDeposito.value,
      montoRetiro:montoRetiro.value,
      monedaRetiro:monedaRetiro.value,
      tazaCambio:tazaCambio.value,
      idBanco:idBanco.value,
      idUsuario:idUsuario.value,
      fechaPedido:fecha.hora,
      fechaCompletada:Date(),
      idOrden:(Date.now() / 300).toString()     
    }

    const socket = io(devServer)
    
    await socket.emit('newPedido',datosPedido)

    socket.on('newPedido', data=>{
            setLoading(data)
            setActiveAlert(true)
            btn(false)
    })
      
     return 
}

function pedidos(id,setTabla){
    const socket = io(devServer)

    socket.emit('pedidos',id)
    socket.on(id, data =>{
            setTabla(data)
    })
    return 
}

function notificationPedido(){
    const sendNotification = async (mensaje)=>{
        const worker = await navigator.serviceWorker.getRegistration()
        if(worker){
            await window.Notification.requestPermission()
            worker.showNotification(mensaje.titulo,{
                body:mensaje.body,
                icon:'/ico.png',
                vibrate: [200,200]
            })
        }else{
            alert(mensaje.titulo,mensaje.body)
                new Notification(mensaje.titulo,{
                    body:mensaje.body,
                    icon:'/ico.png',
                    vibrate: (200,200)
                })
        }
    }
    const user = JSON.parse(sessionStorage.userSesion)
    const iduser = user.idUsuario.toString()
    const socket = io(devServer)
    socket.emit('notificacionPedido',user.correo)
    socket.on('notificacionPedido',mensaje=>{
        if(iduser === mensaje.usuario){
            if(!('Notification' in window) || !('serviceWorker' in navigator)){
                return
            }
            sendNotification(mensaje)
            
        }else{
            return
        }
    })
    return
}
const notificacioNoticias = ()=>{
    const sendNotification = async (mensaje)=>{
        const worker = await navigator.serviceWorker.getRegistration()
        if(worker){
            await window.Notification.requestPermission()
            worker.showNotification(mensaje.titulo,{
                body:mensaje.body,
                icon:'/ico.png',
                vibrate: (200,200)
            })
        }else{
            new Notification(mensaje.titulo,{
                body:mensaje.body,
                icon:'/ico.png',
                vibrate: (200,200)
            })
            alert(mensaje.body)
        }
    }

    const socket = io(devServer)
    
    socket.on('notificacionNoticias',mensaje=>{
            if(!('Notification' in window) || !('serviceWorker' in navigator)){
                alert(mensaje.titulo,mensaje.body)
            }else{
                sendNotification(mensaje) 
            }
    })
}
function pedidosAdministrador(setTabla,limit){
    const socket = io(devServer)
    
    socket.emit('pedidosGenerales',limit)
    socket.on('pedidosGenerales', data =>{
        setTabla(data)
    })     
}
function deletePedido(id, correo){
    const socket = io(devServer)
    const data={
        id,
        correo
    }
    socket.emit('deletePedido',data)
}
function updatePedido(data,referenciaDeposito){
    const useLog = JSON.parse(sessionStorage.userSesion)
    const {id,status,idUsuario,nombreUsuario,correoUsuario} = data
    console.log(data)
    if(!referenciaDeposito){
       const pedido ={
        idPedido:id.value,
        status:status.value,
        idUsuario:idUsuario.value,
        nombreUsuario:nombreUsuario.value,
        correoUsuario:correoUsuario.value
    }
    const socket = io(devServer)
    socket.emit('updatePedido',pedido)
    socket.on('updatePedido',data=>{
        if(data.status && data.idUsuario === useLog.idUsuario.toString()){
            window.location = '/'  
        }
    }) 
    }else{
        const pedido ={
            idPedido:id.value,
            status:status.value,
            idUsuario:idUsuario.value,
            nombreUsuario:nombreUsuario.value,
            correoUsuario:correoUsuario.value,
            referenciaDeposito:referenciaDeposito.value
        }
        const socket = io(devServer)
        socket.emit('updatePedido',pedido)
        socket.on('updatePedido',data=>{
            if(data.status && data.idUsuario === useLog.idUsuario.toString()){
                window.location = '/'  
            }
        })
    }
}

export {paises,pedidos, newOfert, pedidosAdministrador,deletePedido,updatePedido,notificationPedido,addPais,updatePais,notificacioNoticias}