import React, {useState, useEffect, useLayoutEffect} from 'react'
import {paises} from '../../gets_apis/sockets'
import {BtnPedido} from '../../sesiones/userControl'
import vzla from '../images/venezuela.png'

import './calculator.css'

const Calculator = ()=>{
const [sesion, setSesion] = useState(false)

    const [divisas, setDivisas] = useState([{}])

    const [resultado, setResultado] = useState(0)
    const [formula, setFormula] = useState({
        deposito:0,
        tazacambioorigen:0,
        monedaDeposito:'',
        monedaRetiro:'Bolivar Venezolano'
    })

    const monedaOrigen =  e =>{
        e.preventDefault()
        const {tazacambioorigen, alt,} = e.target.attributes
        const newValues = {
            deposito:formula.deposito,
            tazacambioorigen:tazacambioorigen.value,
            monedaDeposito:alt.value,
            monedaRetiro:'Bolivar Venezolano'
        }
        setFormula(newValues)
    }
    const cantidadDeposito = async e =>{
        const {value} = e.target
        const newValues = {
            deposito:value,
            tazacambioorigen: formula.tazacambioorigen,
            monedaDeposito:formula.monedaDeposito,
            monedaRetiro:formula.monedaRetiro
        }
        setFormula(newValues)
    }
    const calcular = () =>{
        const rs = Math.fround(formula.deposito * formula.tazacambioorigen)
        setResultado(rs.toFixed(3))
    }
    useLayoutEffect(()=>{
        paises(setDivisas)
        sessionStorage.userSesion ? setSesion(true): setSesion(false)
    },[])

    useEffect(()=>{
        calcular()
    })

    return (
        <span className='calculadora'>
                    <h2>Calculadora</h2>

                    <h2>
                        de: {divisas.map((items,key)=>
                            <img
                                key={key}
                                tazacambioorigen={items.tazaCambio}

                                alt={items.moneda}

                                src={items.ico}
                                onClick={monedaOrigen}
                             />)
                        }
                    </h2>

                        <h2>{formula.monedaDeposito}</h2>
                    <span>
                        <input type="number" step='0.1' placeholder="200" onChange={cantidadDeposito} onKeyUp={cantidadDeposito}  /> 
                    </span>
                        <h2>a:<img src={vzla} alt='Venezuela'/> Bolivares Venezolanos</h2>
                    <span>
                        <label>{resultado === 'NaN' ? 0 : resultado }</label>
                    </span>
                    
                    <BtnPedido sesion={sesion} formula={formula} resultado={resultado}/>
                    
                </span>
           
    )
}

export default Calculator