import React, {useState, useEffect} from 'react'
import './or.css'
import desc from '../../../svg/descargar1.svg'
import cont from '../../../svg/contrato.svg'
import {updatePedido} from '../../../gets_apis/sockets'

const VerifyOrder = (props)=>{
    const {item} = props.match.params
    const [cliente, setCliente] = useState('')

    const confirmar = e =>{
        e.preventDefault()
        if(e.target.referenciaDeposito.value === ''){
            return alert('llene el campo vacio')
        }
        updatePedido(e.target.attributes,e.target.referenciaDeposito)
        return
    }
useEffect(()=>{
    const usuario = JSON.parse(sessionStorage.userSesion)
    setCliente(usuario)
},[])

    return(
        <div className='container' >
            <h1>Verificar orden {item}</h1>
            <form className="Paso2 " onSubmit={confirmar}
                id={item}
                status='verificada'
                idUsuario={cliente.idUsuario}
                correoUsuario={cliente.correo}
                nombreUsuario={cliente.nombre+' '+cliente.apellido} >
            <div>
                <h2>Informacion de orden</h2>
                <label>Numero de confirmación bancaria:</label>
                <input type="text" name='referenciaDeposito' placeholder="Numero de referencia" defaultValue=''/>
               
                <input
                className='btnGreen' 
                type='submit' value='Confirmar' />
            </div>
                
            <div>
                <h2>Para transacciones en efectivo</h2>
                <select name='vcoin'>
                    <option>cuenta VcoinTransfer</option>
                    <option>cuenta VcoinTransfer2</option>
                    <option>cuenta VcoinTransfer2</option>
                </select>
                <button className="BtnDescarga"><img src={desc} alt="Descargar libreta" /><label>Imprimir libreta</label> </button>
                <button className="BtnDescarga"><img src={cont} alt="Descargar libreta" /><label>descargar libreta</label> </button>
                
                
            </div>
             
        </form>
        </div>
    )
}

export default VerifyOrder