import React, {Fragment, useState, useEffect} from'react'
import {Link} from 'react-router-dom'
import {updatePedido,pedidosAdministrador,pedidos} from '../../gets_apis/sockets'
import palometa from '../../svg/palometa.svg'
import detalles from '../../svg/detalles.svg'
import cerrarDetalles from '../../svg/cerrar.svg'
import advertencia from '../../svg/advertencia.svg'
import enviado from '../../svg/enviado.svg'


const Pedidos = ()=>{
        const user = JSON.parse(sessionStorage.userSesion)
        const [tabla, setTabla] = useState([{}])

       const getUpdate = e =>updatePedido(e.target.attributes)

       const show = e =>{
           e.target.classList.toggle('detalles')
       }

        useEffect(()=>{
                if(user.rango !== 'administrador'){
                        pedidos(user.idUsuario,setTabla)
                }else{
                       pedidosAdministrador(setTabla)
                }
        },[])
 return(
     <div className='container'>

<Fragment>  
                <h1>Ultimos pedidos</h1>
                <section class="table">
            <div class="thead">
                <h2 class="IDth">ID</h2>
                <h2>Fecha</h2>
                <h2>Monto</h2>
                <h2>Destino</h2>
                <h2>Detalles</h2>
            </div>
            <div class="tbody">
                    {
                        tabla.map((items,i)=>{
                            return items.status === 'rechazada' || items.status === 'cancelada' || items.status === 'completada'?(
                                <Fragment>
                                <span class="tr">
                                        <div class="td IDtr">
                                            {items.idOrden}
                                        </div>
                                        <div class="td">
                                            {items.fechaPedido}
                                        </div>
                                        <div class="td">
                                            {items.montoDeposito}
                                            
                                        </div>
                                        <div class="td">
                                            <span>
                                                {items.montoRetiro} 
                                            </span>
                                        </div>
                                        
                                            <p class="status">
                                                {items.status}
                                            </p>
           
                                {
                                    items.status === 'abierta' ? (
                                        <Fragment>
                                        <img onClick={getUpdate} id={items.idPedido} status='cancelada' idUsuario={items.idUsuario} src={cerrarDetalles} alt=""/>
                                        </Fragment>
                                    ): null
                                } 
                                {
                                    items.status === 'aceptada' ?(
                                        <Fragment>
                                        <Link to='/Dashboard/VerifyOrder'>
                                        <img id={items.idPedido} status='verificar' src={palometa} alt=""/>
                                        </Link>
                                        
                                        <Link to='/canlcel'>
                                        <img id={items.idPedido} status='verificar' src={cerrarDetalles} alt=""/>
                                        </Link>
                                        </Fragment> 
                                        ):null
                                }
           
                            <img  src={detalles} alt=""
                                id="cerrar" onClick={show}/>
           
                            <div className="tdSub">
                            <article class="DetallesTabla">
                                <div>
                                    <img src={enviado} alt=""/>
                                    <span>
                                        <h3>Orden status: {items.status}</h3>
                                        <p>Id de orden {items.idOrden}</p>
                                    </span>
                                </div>
                                <article class="DatosDetalles">
                                    <div>
                                        <h2>Remitente</h2>
                                        <p>
                                           <span>Tipo Moneda:</span> {items.monedaDeposito}
                                            <br/>
                                            <span>Nombres:</span> {items.nombre} {items.apellido}
                                            <br/>
                                            <span>Doc. Identidad:</span> {items.dni}
                                            <br/>
                                            <span>Direccion:</span> {items.direccion}
                                            <br/>
                                            
                                            <span>Correo:</span> {items.correo}
                                        </p>
                                    </div>
                                    <img src={advertencia} alt=""/>
                                    <div>
                                        <h2>destinatario</h2>
                                        <p>
                                           <span>Pais:</span> {items.paisBanco}
                                            <br/>
                                            <span>Nombres:</span> {items.titular}
                                            <br/>
                                            <span>Doc. Identidad:</span> {items.dniTitular}
                                            <br/>
                                            <span>Banco:</span> {items.banco}
                                            <br/>
                                            <span>Numero de cuenta:</span>
                                            {items.numeroCuenta}
                                            <br/>
                                            
                                        </p>
                                    </div>
                                    <span>
                                       <p>
                                            monto: <span>
                                                50 soles peruanos
                                            </span>
                                        </p>
                                        <p>
                                            Taza del dia: <span>
                                                5.000Bsf
                                            </span>
                                        </p>
                                        
                                    </span>
                                    <br/>
                                    <span>
                                       <p>
                                       referencia bancaria: <span>
                                                000-456456-456456-456
                                            </span>
                                        </p>
                                        <p>
                                            total recibir: <span class="TotalDestino">
                                                21.545.115.000Bsf
                                            </span>
                                        </p>                       
                                    </span>
                                </article>
                                <div className="Botones">
                                <p className="btnBlue">descargar</p> <p className="btnBlue" >Imprimir</p>
                                </div>
                                
                            </article>
                                </div>
                            </span>

                       </Fragment>
                            ):null
                        })
                            
                    }
              </div>
        </section>       
        </Fragment>   
        
     </div>
 ) 
} 

export default Pedidos