import React, {useState, useEffect} from 'react'
import {Redirect} from 'react-router-dom'
import {addBank} from '../../../gets_apis/api_sesion'
import {Alerta} from '../../../components/alerts/alerts'
import Loading from '../../../components/loading'
import './addBanks.css'

function Redir(){
    return<Redirect to='/Dashboard/BankAcounts'/>
}
const AddBanks = ()=>{
    const [usuario, setUsuario] = useState(false)
    const [loading, setLoading] = useState('')
    const [activeAlert, setActiveAlert] = useState(false)

    const add = e =>{
        setLoading('cargando')
        e.preventDefault()
        const {paisBanco, banco, tipoCuenta, numeroCuenta, idUsuario, titular, dniTitular, nacional} = e.target
        const data = {
            paisBanco: paisBanco.value,
            banco: banco.value, 
            tipoCuenta: tipoCuenta.value, 
            numeroCuenta: numeroCuenta.value,
            idUsuario: idUsuario.value,
            titular:titular.value,
            dniTitular:dniTitular.value,
            nacional:nacional.value 
        }
        
        if(nacional.value === '' || paisBanco.value === '' || banco.value === '' || tipoCuenta.value === '' || numeroCuenta.value === '' || idUsuario.value === '' || dniTitular.value === '' || titular.value === ''){
            alert('Debes llenar todos los campos')
            return
        }else{
            addBank(data,setLoading,setActiveAlert)
            document.querySelector('.AgregarBanco').reset()
        }
    }
    useEffect(()=>{
        const getUser = JSON.parse(sessionStorage.userSesion)
        setUsuario(getUser)
    })
    return(
            <div className='container'>
                <form className="AgregarBanco" onSubmit={add}>
                <h2>Informacion de de cuenta</h2><br/>
                <div>
                <label >Pais:</label>
                <select name="paisBanco" >
                    <option value="">selecione el pais del banco</option>
                    <option value="venezuela">venezuela</option>
                    <option value="argentina">argentina</option>
                </select>

                <label>Banco:</label>
                <input type="text" name='banco' placeholder="Nombre del Banco"/>

                <label>Tipo de cuenta:</label>
                <select name="tipoCuenta" >
                    <option value="">selecione el tipo de Cuenta</option>
                    <option value="corriente/cheque">Corriente/cheque</option>
                    <option value="ahorro">Ahorro</option>
                </select>

                <label>Nombre del Titular:</label>
                <input type="text" name='titular' placeholder="Nombre y apellido"/>

                </div>

                <div>
                <label >Documento de identidad del titular:</label>
                <input type="number" name='dniTitular' placeholder="DNI/C.I/Pasaporte"/>

                <label>Nacional/extranjero:</label>
                <select name="nacional" >
                    <option value="">Eres nacional/ extranjero</option>
                    <option value="Nacional">Nacional</option>
                    <option value="Extranjero">Extranjero</option>
                </select>

                <label>Numero De Cuenta:</label>
                <input type="number" name='numeroCuenta' placeholder="Numero de Cuenta Bancaria"/>

                <input type='hidden' name='idUsuario' value={usuario.idUsuario} />
                {
                    loading === 'cargando' ? <Loading/> : null
                }
                {
                    loading === 'registro exitoso'? <Redir/>:null
                }
                </div>
                <Alerta activeAlert={activeAlert} setActiveAlert={setActiveAlert} mensaje='Completado'/>
                <button className="btnGreen">Agregar</button >
            </form>
            </div>        
    )
}

export default AddBanks