import React, {Fragment, useState, useEffect} from 'react'
import './pwa_feactures.css'
let i = null
function PwaFeactures(){
    const [install, setInstall] = useState(null)
    
    const [notification, setNotification] = useState(null)
    useEffect(()=>{
          
        if(!('Notification' in window) || !('serviceWorker' in navigator)){
               return 
        }else{
            window.addEventListener('beforeinstallprompt', async e =>{
            i = e
            await setInstall(<div className="btnBlue" onClick={installApp} >Install</div>)

                if(Notification.permission !== 'granted'){
                    setNotification(<div className="btnGreen" onClick={activateN} >Notification</div>) 
                    }
            
            }) 
        }
       
       
    })

    const activateN = async ()=>{
            
        const regiter = await navigator.serviceWorker.getRegistration()

    if(Notification.permission === 'default'){
        await Notification.requestPermission()
        regiter.showNotification('Gracias por aceptar',{
            icon:'/logo.png'
        })
    }
    if(Notification.permission !== 'granted'){
        await Notification.requestPermission()
        regiter.showNotification('Gracias por aceptar',{
            icon:'/logo.png'
        })
    }
    if(Notification.permission === 'blocked'){
        alert('Bloqueaste las notificaciones anteriormente')
    }
    
    }

    const installApp = (e)=>{
        i.prompt()
    }
    return(
        <Fragment>
            <div className='notification'>
                    {install}
                    {notification}
            </div>
            
            
        </Fragment>
    )
}

export default PwaFeactures