import React,{Fragment, useEffect} from 'react'
import {Link} from 'react-router-dom'
import './header_dashboard.css'
import profile from '../components/images/team-member01-150x150.jpg'
import {notificationPedido, notificacioNoticias} from '../gets_apis/sockets'


const datosFecha = new Date()
const Headerdashboard = (props)=>{
    const {user, sesion, setSesion} = props

   const fecha ={
      
       hora:datosFecha.toLocaleDateString()
   }
   const show = ()=>{
    document.querySelector('#navigation').classList.toggle('show')
    }
   const logout = async () =>{
        await sessionStorage.removeItem('userSesion')
        setSesion(false)
   }
useEffect(()=>{
    notificationPedido()
    notificacioNoticias()
    
},[])
    return sesion?(
        <Fragment>
            <aside className='scroll' id='navigation'>
            <span onClick={show}>menu</span>
        <div>
            <img src={profile} alt=""/>
            <div>
            <h3>Welcome {user.nombre} {user.rango}</h3>
            <p>{fecha.hora}</p>
            </div>
        </div>
                    <Link to="/Dashboard">Tablero</Link>
                    <Link to="/Dashboard/BankAcounts">Cuentas Bancarias</Link>
                    <Link to="/Dashboard/AddBanks">Agregar Cuentas Bancarias</Link>
                    {
                       user.rango === 'cliente' ?( <Link to="/Dashboard/NewOrder">Nueva Orden</Link>):null
                    }
                    <Link to="/Dashboard/History">Historial</Link>
                    {
                       user.rango !== 'cliente' ?( <Link to="/Dashboard/AddPais">Gestionar monedas</Link>):null
                    }
                    <Link to="/Dashboard/Settings">Configuración</Link>
                    <Link onClick={logout} to="/">Salir</Link>
    </aside>
        </Fragment>
    ):null
}
export default Headerdashboard